
.container{
    height: 100vh;
    background-image:linear-gradient(0deg,
    rgba(16, 15, 15, 0.75),
    rgba(0, 0, 0, 0.75)), url("../Images/background.jpg");
    background-size: cover;
    background-color: #3d3d3d;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container .header{
    display: flex;
    flex-wrap: wrap;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    margin-top: -0.8em;
}
.container .header img{
    width: 13em;
}
.container .header .btns{
    display: flex;
    align-items: flex-end;
    flex-direction: row;
}
.container .header .btns button{
    width: 7em;
    height: 2.8em;
    color: white;
    font-weight: bold;
    font-size:13.5px
}
.container .header .btns #lang{
   background-color: rgb(0, 0, 0);
   margin-right: 1.5em;
   border: 1px solid white;
   border-radius: 5px;
}
.container .header .btns #log{
    background-color: rgb(219, 0, 0);
    border: 1px solid black;
    border-radius: 5px;
}
.container .header .btns #log:hover{
    cursor: pointer;
}

.container .headerdisc,.faqBox .headerdisc{
    color: white;
    height: 70%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    line-height: 1;
    margin-top: 3.5em;
}
.container .headerdisc p{
    font-size: 1.8em;
    margin-bottom: 0;
    color: white;
}
.container .headerdisc p:first-child {
    font-size: 4em;
    font-weight: bold;
}

.container .headerdisc span,.faqBox .headerdisc span {
    font-size: 1.2em;
    margin-top: 1.5em;
}
.container .headerdisc form,.faqBox .headerdisc form{
    margin-top: 1.5em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.container .headerdisc form input[type="email"],.faqBox .headerdisc form input[type="email"]{
    width: 38em;
    height: 4.8em;
    border: 1px solid black;
    border-radius: 2px 0 0 2px;
}
.container .headerdisc form button,.faqBox .headerdisc form button{
    width: 18em;
    height: 5.1em;
    border: 1px solid black;
    background-color: rgb(219, 0, 0);
    border-radius: 0 2px 2px 0;
}
.container .headerdisc form button,.faqBox .headerdisc form button:hover{
    cursor: pointer;
}
.container .headerdisc form button span, .faqBox .headerdisc form button span{
    color: white;
    font-size: 2em;
}

.enjoy{
    margin-top: 0.5em;
    height: 30em;
    display: flex;
    flex-wrap: wrap;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
}
.enjoy .content{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 32em;
    justify-content: center;
}
.enjoy .content p{
    font-size: 3.2em;
    font-weight: bold;
    margin-bottom: 0.4em;
}
.enjoy .content span{
    font-size: 1.6em;
}
.enjoy img{
    width: 35em;
    margin-left: 3.2em;
}
.mobile{
    margin-top: 0.5em;
    height: 30em;
    display: flex;
    flex-wrap: wrap;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
}
.mobile .imgCont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 8em;
}
.mobile .imgCont img{
    width: 31em;
}
.mobile .imgCont .box{
    width: 20em;
    height: 5em;
    margin-top: -9em;
    border: 2px solid rgb(66, 66, 67);
    display: flex;
    background-color: black;
    padding: 10px;
    border-radius: 10px;
}

.mobile .imgCont .box img{
    width: 60px;
    height: 80px;
}
.mobile .imgCont .box .boxDetails{
    line-height: 0;
    margin-left: 10px;
}
.mobile .imgCont .box .boxDetails p{
    color: #4d7df7;
}
.mobile .content{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 35em;
    justify-content: center;
}
.mobile .content p{
    font-size: 3.2em;
    font-weight: bold;
    margin-bottom: 0.4em;
}
.mobile .content span{
    font-size: 1.6em;
}
.faqBox{
    margin-top: 0.5em;
    background-color: black;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}
.faqBox>p{
    font-size: 3.1em;
    font-weight: bold;
}
.faqBox .box{
    background-color: #303030;
    margin-bottom: 8px;
    width: 51em;
    height: 4.5em;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}
.faqBox .box p{
    font-size: 1.8em;
    padding: 0 10px 0 20px;
}
.faqBox .box span{
    font-size: 3em;
    margin-right: 15px;
    margin-bottom: 10px;
}
.faqBox .headerdisc{
    margin-bottom: 4em;
}

.footbar{
    background-color: black;
    margin-top: 0.5em;
    color:#737373;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footbar .cont{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.footbar .cont .box{
    margin-right: 50px;
}
.footbar .cont ul li{
    list-style-type: none;
    padding: 5px;
}
.footbar .cont ul li a{
    text-decoration: none;
    color: #737373;
    font-size: 0.8em;
}
.footbar button{
    width: 10em;
    height: 4em;
    background-color: black;
    color: grey;
    border: 2px solid white;
    padding: 10px;
    border-radius: 4px;
}
.footbar button span{
    font-size: 1.4em;
}
.footbar p:last-child{
    font-size: 0.8em;
}


/* ......................Media Query...................... */

@media only screen and (max-width:1200px){

.container .headerdisc p{
    font-size: 1.5em;
}
.container .headerdisc p:first-child {
    font-size: 3em;
}
.container .headerdisc span,.faqBox .headerdisc span {
    font-size: 1em;
}
.container .headerdisc form input[type="email"],.faqBox .headerdisc form input[type="email"]{
    width: 30em;
    height: 4em;
}
.container .headerdisc form button,.faqBox .headerdisc form button{
    width: 15em;
    height: 4em;
    margin-top: 1em;
}

.enjoy .content{
    width: 30em;
}
.enjoy .content p{
    font-size: 2.5em;
}
.enjoy .content span{
    font-size: 1.3em;
}
.enjoy img{
    width: 25em;
    margin-left: 1em;
}


.mobile .imgCont img{
    width: 25em;
}
.mobile .imgCont .box{
    width: 14em;
    height: 4em;
    margin-top: -9em;
}
.mobile .imgCont .box img{
    width: 50px;
    height: 60px;
}
.mobile .content{
    width: 25em;
}
.mobile .content p{
    font-size: 2.5em;
}
.mobile .content span{
    font-size: 1.3em;
}

}

@media only screen and (max-width:992px){
    .container{
        height: 75vh;
    }
    .container .header{
        margin-top: 0.2em;
    }
    .container .header img{
        width: 8em;
    }
    .container .header .btns{
        margin-right: 0.2em;
    }
    .container .header .btns button{
        width: 5em;
        height: 2.5em;
        font-size:12px
    }
    .container .headerdisc{
        height: 60%;
    }
    .container .headerdisc p{
        font-size: 1em;
    }
    .container .headerdisc p:first-child {
        font-size: 2em;
    }
    .container .headerdisc span,.faqBox .headerdisc span {
        font-size: 0.8em;
    }
    .container .headerdisc form input[type="email"],.faqBox .headerdisc form input[type="email"]{
        width: 25em;
        height: 3em;
    }
    .container .headerdisc form button,.faqBox .headerdisc form button{
        width: 12em;
        height: 3em;
        margin-top: 1em;
    }
    .container .headerdisc form button span,.faqBox .headerdisc form button span{
        font-size: 1em;
    }

    .enjoy{
        flex-direction:column-reverse;
        justify-content: center;
        align-items: center;
    }
    .enjoy .content{
        text-align: center;
        width: 30em;
    }
    .enjoy .content p{
        font-size: 2em;
    }
    .enjoy .content span{
        font-size: 1.2em;
    }

  
    .mobile{
        flex-direction: column;
    }
    .mobile .imgCont{
        margin-right: 0em;
    }
    .mobile .imgCont img{
        width: 25em;
    }
    .mobile .imgCont .box{
        width: 15em;
        height: 4em;
    }
    .mobile .content{
        margin-top: 1em;
        width: 28em;
        text-align: center;
    }
    .mobile .content p{
        font-size: 2em;
    }
    .mobile .content span{
        font-size: 1.2em;
    }


    .faqBox>p{
        font-size: 2.5em;
    }
    .faqBox .box{
        width: 30em;
        height: 4em;
    }
    .faqBox .box p{
        font-size: 1.5em;
    }
    .faqBox .box span{
        font-size: 2em;
        margin-right: 15px;
        margin-bottom: 15px;
    }
}

